<template>
  <div id="app">
    <transition name="push">
      <router-view/>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
  .push-enter-active {transition: all .5s ease;}
  .push-leave-active {transition: all .5s ease;}
  .push-enter, .push-leave-to {transform: translateX(10px);opacity: 0;}

</style>



