<template>
  <div class="app-nav-bar" style="display: flex;flex-direction: row;justify-content: flex-start;align-items: center;background-color: #00b3ae;width: 100%;height: 50px;">
    <a @click="back" style="color: white;flex: 2;display: inline-block;height: 100%;line-height: 50px;">< 返回</a>
    <div style="flex: 6;height: 100%;"></div>
    <a @click="Alert" style="color: white;flex: 2;display: flex;flex-direction: row;justify-content: center;align-items: center;">
      <div style="padding: 8px; border-radius: 5px;border: 1px solid white;width: 25px;height: 18px;display: flex;flex-direction: column;justify-content: space-around;align-items: center;">
        <span style="display: block;height: 2px;background-color: white;width: 30px;"></span>
        <span style="display: block;height: 2px;background-color: white;width: 30px;"></span>
        <span style="display: block;height: 2px;background-color: white;width: 30px;"></span>
      </div>
    </a>
  </div>
</template>
<script>
export default {
  name: 'NavBar',
  data () {
    return {}
  }, // 页面数据
  mounted () {}, // 生命周期钩子函数
  computed: {}, // 计算属性
  methods: {
    back: function () {
      history.back()
    },
    Alert: function () {
      alert('更多菜单')
    }
  }, // 事件函数
  watch: {} // 数据监听
}
</script>
<style scoped>
</style>



