<template>
  <div>
    <nav-bar></nav-bar>
    <h1 @click="jump">Sub page</h1>
  </div>
</template>
<script>
import NavBar from './NavBar.vue'
export default {
  components: {NavBar},
  name: 'Hello',
  data () {
    return {}
  }, // 页面数据
  mounted () {}, // 生命周期钩子函数
  computed: {}, // 计算属性
  methods: {
    jump: function () {
      this.$router.push({path: '/one'})
    }
  }, // 事件函数
  watch: {} // 数据监听
}
</script>
<style scoped>
</style>



