<!-- create by yanshan -->
<template>
  <div class="container">
    <nav-bar></nav-bar>
    <!--<div>-->
      <h1>四舍五入：{{oldValue}} => <h3>{{newValue}}</h3></h1>
      <br>
      <h1>非四舍五入：{{oldValue}} => <h3>{{newValue2}}</h3></h1>
    <!--</div>-->
  </div>
</template>

<script>
  import NavBar from '../components/NavBar.vue'

  export default {
    components: {NavBar},
    data () {
      return {
        oldValue: 16.0254846,
        newValue: 0,
        newValue2: 0
      }
    },
    mounted () {
      this.newValue = this.$SL.FloatToFixedY(this.oldValue)
      this.newValue2 = this.$SL.FloatToFixedN(this.oldValue)
    }
  }
</script>

<style scoped>

</style>




