<!-- create by yanshan -->
<template>
  <div class="container">
    <nav-bar></nav-bar>
    <input v-model="number">
    <span>{{result}}</span>
  </div>
</template>

<script>
  import NavBar from '../components/NavBar.vue'

  export default {
    components: {NavBar},
    data () {
      return {
        number: null,
        result: ''
      }
    },
    watch: {
      number () {
        if (this.$SL.IsEmailNumber(this.number)) {
          this.result = '是邮箱'
        } else {
          this.result = '该号码不是邮箱'
        }
      }
    }
  }
</script>

<style scoped>
  input {display: block;height: 40px;line-height: 40px;font-size: 18px;color: darkcyan;}
</style>




