<!-- create by yanshan -->
<template>
  <div class="container">
    <span>
      <li v-for="item in datas" @click="to(item.event)">
        <a v-if="item.event.length > 0" style="color: darkcyan;">{{item.name}}</a>
      </li>
    </span>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        datas: [
          {name: '数据持久化 localstrong', event: '/sailei-localstorage'},
          {name: '数据持久化 sessionstrong', event: '/sailei-sessionstorage'},
          {name: '浏览器判断识别', event: '/sailei-browser'},
          {name: '平台判断识别', event: '/sailei-os'},
          {name: '小数点转换', event: '/sailei-point'},
          {name: '手机号正则匹配', event: '/sailei-phone'},
          {name: '邮箱正则匹配', event: '/sailei-email'},
          {name: '通用网络请求 get', event: ''},
          {name: '通用网络请求 post', event: ''},
          {name: '通用网络请求 上传', event: ''},
          {name: '通用网络请求 下载', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''},
          {name: '手机号正则匹配', event: ''}
        ]
      }
    },
    methods: {
      to: function (url) {
        console.log('点击跳转')
        this.$router.push(url)
      }
    },
    mounted () {}
  }
</script>

<style scoped>
  .container {width: 100%;}
  .container>span {width: 100%;display: block;}
  .container>span>li {display: block;height: 40px;border-bottom: 1px solid ghostwhite;justify-content: flex-start;text-align: left;padding: 0 10px;background-color: white;}
  .container>span>li:active {background-color: rgba(0, 0, 0, 0.1);}
  .container>span>li>a {display: inline-block;height: 40px;line-height: 40px;text-decoration: none;font-size: 16px;color: gray;}
</style>




