<!-- create by yanshan -->
<template>
  <div class="container">
    <nav-bar></nav-bar>
    <h1>LocalStorage</h1>
    <div class="content">
      {{contentData}}
    </div>
    <div v-for="item in datas" class="btn" @click="localStorage(item.type)">{{item.name}}</div>
  </div>
</template>

<script>
  import NavBar from '../components/NavBar.vue'
  export default {
    components: {NavBar},
    data () {
      return {
        contentData: null,
        datas: [
          {name: '保存数据', type: 0},
          {name: '获取数据', type: 1},
          {name: '删除一个数据', type: 2},
          {name: '清楚所有数据', type: 3},
          {name: '保存对象', type: 4},
          {name: '获取对象', type: 5}
        ]
      }
    },
    methods: {
      localStorage: function (type) {
        if (type === 0) {
          this.$SL.LocalStorageSet('key=0', '4654634afdasfasdtfg')
        } else if (type === 1) {
          let value = this.$SL.LocalStorageGet('key=0')
          this.contentData = value
          console.log(value)
        } else if (type === 2) {
          this.$SL.LocalStorageRemove('key=0')
          let value = this.$SL.LocalStorageGet('key=0')
          this.contentData = value
        } else if (type === 3) {
          this.$SL.LocalStorageClearAll()
          let value = this.$SL.LocalStorageGet('key=0')
          this.contentData = value
        } else if (type === 4) {
          this.$SL.LocalStorageObjectSave('key=1', {
            name: '小明',
            sex: '男'
          })
        } else if (type === 5) {
          let obj = this.$SL.LocalStorageObjectGet('key=1')
          console.log(obj)
          this.contentData = obj
        }
      }
    }
  }
</script>

<style scoped>
  .btn {height: 40px;line-height: 40px;background-color: white;border-bottom: 1px solid gainsboro;}
  .btn:active {background-color: gainsboro;}
  .content {height: 200px;}
</style>




