import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Load from '../components/Loading.vue'
import Sub from '../components/Sub.vue'
import One from '../components/One.vue'
import Two from '../components/Two.vue'
import Three from '../components/Three.vue'
import SaiLei from '../page/Sailei.vue'
import LocalStorage from '../page/LocalStorage.vue'
import SessionStorage from '../page/SessionStorage.vue'
import Browser from '../page/Browser.vue'
import Os from '../page/Os.vue'
import Point from '@/page/Point'
import PhoneNumber from '../page/PhoneNumber.vue'
import EmailNumber from '../page/EmailNumber.vue'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {
      path: '/load',
      name: 'Load',
      component: Load
    },
    {
      path: '/sub',
      name: 'Sub',
      component: Sub
    },
    {
      path: '/one',
      name: 'One',
      component: One
    },
    {
      path: '/two',
      name: 'Two',
      component: Two
    },
    {
      path: '/three',
      name: 'Three',
      component: Three
    },
    {
      path: '/',
      name: 'SaiLei',
      component: SaiLei
    },
    {
      path: '/sailei-localstorage',
      name: 'LocalStorage',
      component: LocalStorage
    },
    {
      path: '/sailei-sessionstorage',
      name: 'SessionStorage',
      component: SessionStorage
    },
    {
      path: '/sailei-browser',
      name: 'Browser',
      component: Browser
    },
    {
      path: '/sailei-os',
      name: 'Os',
      component: Os
    },
    {
      path: '/sailei-point',
      name: 'Point',
      component: Point
    },
    {
      path: '/sailei-phone',
      name: 'PhoneNumber',
      component: PhoneNumber
    },
    {
      path: '/sailei-email',
      name: 'EmailNumber',
      component: EmailNumber
    }
  ]
})



