<template>
  <div class="container">
    <nav-bar></nav-bar>
    <div v-show="!dataLoaded" class="loading-container">
      <span class="iconfont loading-animate">&#xe691;</span>
      <span>请稍后...</span>
    </div>
    <div class="content">
      <!--页面内容-->
      <h1 @click="jump">Two</h1>
    </div>
  </div>
</template>
<script>
import NavBar from './NavBar.vue'
export default {
  components: {NavBar},
  name: 'Two',
  data () {
    return {
      dataLoaded: false
    }
  }, // 页面数据
  mounted () {
    console.log('This is mounted')
    var self = this
    setTimeout(function () {
      self.dataLoaded = true
    }, 500)
  }, // 生命周期钩子函数
  computed: {}, // 计算属性
  methods: {
    jump: function () {
      this.$router.push({path: '/three'})
    }
  }, // 事件函数
  watch: {} // 数据监听
}
</script>
<style scoped>
.loading-container {height: 50px;display: flex;flex-direction: row;justify-content: center;align-items: center;}
.loading-container>span {display: inline-block;}
.loading-animate {width: 50px;height: 50px;line-height: 50px;text-align: center; font-size: 30px;animation-name: loadAnimate;animation-duration: 3s;animation-iteration-count: infinite;animation-timing-function: linear;}
@keyframes loadAnimate {
  0% {transform: rotateZ(0deg) scale(1.0);color: red;}
  50% {transform: rotateZ(180deg) scale(1.5);color: orange;}
  100% {transform: rotateZ(360deg) scale(1.0);color: red;}
}
</style>
